function BootstrapDialogTitle(props) {
  const { children, onCloseDialog, ...other } = props;

  return (
    <DialogTitle sx={{ m: 0, p: 2 }} {...other}>
      {children}
      {onCloseDialog ? (
        <IconButton
          aria-label="close"
          onClick={onCloseDialog}
          sx={{
            position: 'absolute',
            right: 8,
            top: 8,
            color: (theme) => theme.palette.grey[500],
          }}
        >
        
        </IconButton>
      ) : null}
    </DialogTitle>
  );
}

